import React, { useRef } from "react";
import store from "@/MST/store";
import { observer } from "mobx-react-lite";
import { Link } from "react-router-dom";
import { AuthForm } from "@/components";
import Button from "../elements/Button";

import { AuthFormType } from "@/types/common";

import styles from "./AppBar.module.scss";

type Props = {};

const AppBar: React.FC<Props> = () => {
  const modalRef = useRef<HTMLDialogElement>(null);

  const { isAuthenticated } = store.user;
  const { toggleMenu } = store.interface;

  const onAuthBtnClick = () => {
    if (isAuthenticated) {
      store.user.setLoggedIn(false);
    } else {
      modalRef.current!.showModal();
    }
  };

  const onMenuBtnClick = () => {
    toggleMenu();
  };

  const onLoginClick = ({ type, email, password }: AuthFormType) => {
    console.log(type, email, password);
    closeAuthForm();
    store.user.setLoggedIn(true);
  };

  const onRegisterClick = ({ type, email, password }: AuthFormType) => {
    console.log(type, email, password);
    closeAuthForm();
  };

  const closeAuthForm = () => {
    modalRef.current!.close();
  };

  return (
    <header>
      <ul className={styles.navMenu}>
        {isAuthenticated ? (
          <li>
            <Button icon="icon_menu" title="menu" onClick={onMenuBtnClick} />
          </li>
        ) : null}
        <li>Login</li>
        <li>
          <Link to="/cabinet">Cabinet</Link>
        </li>
        <li>
          <Link to="/">Landing</Link>
        </li>
        <li>
          <Button
            onClick={onAuthBtnClick}
            text={isAuthenticated ? "log out" : "log in / register"}
          />
        </li>
      </ul>
      <dialog ref={modalRef}>
        <AuthForm
          onCloseClick={closeAuthForm}
          onLoginClick={onLoginClick}
          onRegisterClick={onRegisterClick}
        />
      </dialog>
    </header>
  );
};

export default observer(AppBar);
