import Button from "@/components/elements/Button";
import { AuthFormType } from "@/types/common";
import React, { useState } from "react";
import styles from "./AuthForm.module.scss";

type Props = {
  onCloseClick: () => void;
  onLoginClick: (arg: AuthFormType) => void;
  onRegisterClick: (arg: AuthFormType) => void;
};

const AuthForm: React.FC<Props> = ({
  onCloseClick,
  onLoginClick,
  onRegisterClick,
}) => {
  const [registerMode, setRegisterMode] = useState(false);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [passwordRepeat, setPasswordRepeat] = useState("");
  const [error, setError] = useState("");

  const onInputChange =
    (type: string) => (e: React.ChangeEvent<HTMLInputElement>) => {
      const value = e.target.value;
      switch (type) {
        case "email":
          setEmail(value);
          break;
        case "password":
          setPassword(value);
          break;
        case "passwordRepeat":
          setPasswordRepeat(value);
          break;
      }
    };

  const loginClick = () => {
    setError("");
    setRegisterMode(false);
    if (!validateFields()) return;
    onLoginClick({ type: "login", email, password });
  };

  const registerClick = () => {
    setError("");
    setRegisterMode(true);
    if (!validateFields()) return;
    onRegisterClick({ type: "register", email, password });
  };

  const closeClick = () => {
    clearForm();
    onCloseClick();
  };

  const validateFields = () => {
    if (!email || !email.match(/^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/)) {
      setError("email");
      return false;
    }
    if (!password || password.length < 8) {
      setError("password");
      return false;
    }
    if (registerMode && password !== passwordRepeat) {
      setError("passwordRepeat");
      return false;
    }
    return true;
  };

  const clearForm = () => {
    setEmail("");
    setPassword("");
    setPasswordRepeat("");
    setError("");
  };

  return (
    <div className={styles.authForm}>
      <Button
        title={"close"}
        className={styles.closeBtn}
        icon="icon_close"
        onClick={closeClick}
      />
      <div className={styles.fieldsWrapper}>
        <label>
          Email
          <input
            autoFocus={true}
            className={error === "email" ? styles.error : ""}
            value={email}
            type="email"
            placeholder="email"
            onChange={onInputChange("email")}
          />
        </label>

        <label>
          Password
          <input
            className={error === "password" ? styles.error : ""}
            value={password}
            type="password"
            placeholder="password (minimum 8 characters)"
            onChange={onInputChange("password")}
          />
        </label>

        {registerMode ? (
          <label>
            Password repeat
            <input
              className={error === "passwordRepeat" ? styles.error : ""}
              value={passwordRepeat}
              type="password"
              placeholder="password repeat"
              onChange={onInputChange("passwordRepeat")}
            />
          </label>
        ) : null}
      </div>
      <div className={styles.buttonsWrapper}>
        <Button text="Login" onClick={loginClick} />
        <Button text="Register" onClick={registerClick} />
      </div>
    </div>
  );
};

export default AuthForm;
